import { BskyAgent } from '@atproto/api';

document.addEventListener('DOMContentLoaded', () => {
  const loginForm = document.getElementById('loginForm');
  const scheduleForm = document.getElementById('scheduleForm');
  const stopScheduleButton = document.getElementById('stop-schedule');

  let loggedInUsername = null;
  let loggedInApi = null;

  loginForm.addEventListener('submit', async (event) => {
    event.preventDefault();

    const username = document.getElementById('loginUsername').value;
    const password = document.getElementById('loginPassword').value;

    try {
      const api = new BskyAgent({ service: 'https://bsky.social' });
      await api.login({ identifier: username, password });

      loggedInUsername = username;
      loggedInApi = api;

      loginForm.style.display = 'none';
      scheduleForm.style.display = 'block';
      alert('Logged in successfully!');
    } catch (error) {
      console.error('Error logging in:', error);
      alert('Error logging in');
    }
  });

  
  scheduleForm.addEventListener('submit', async (event) => {
    event.preventDefault();
  
    const message = document.getElementById('message').value;
    const time = document.getElementById('time').value;
  
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ username: loggedInUsername, accessToken: loggedInApi.session.accessJwt, message, time }),
    };
  
      try {
        const response = await fetch('/schedule', requestOptions);
        if (response.ok) {
          alert('Scheduled successfully');
        } else {
          alert('Error scheduling the post');
        }
      } catch (error) {
        console.error('Error scheduling the post:', error);
        alert('Error scheduling the post');
      }
    });
  
    stopScheduleButton.addEventListener('click', async () => {
      try {
        const response = await fetch('/stop-schedule', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ username: loggedInUsername }),
        });
  
        if (response.status === 200) {
          alert('Schedule stopped successfully!');
        } else {
          alert('Failed to stop schedule. No active schedule found for this user.');
        }
      } catch (error) {
        console.error('Error stopping the schedule:', error);
        alert('Failed to stop schedule. Please try again later.');
      }
    });
  });
